/* eslint-disable no-return-assign */
/* eslint-disable no-param-reassign */
/* eslint-disable arrow-body-style */
import { types, getSnapshot, onSnapshot } from 'mobx-state-tree';
import request from '../utils/api/request';

const eventModel = types.model('eventModel', {
    holiday: types.boolean,
    month: types.number,
    day: types.number,
    type: types.string,
    title: types.string,
    isBookmark: types.optional(types.boolean, false),
});
const eventStore = types.model('eventStore', {
    loading: types.optional(types.boolean, true),
    hasError: types.optional(types.boolean, false),
    day: types.maybeNull(types.number),
    month: types.maybeNull(types.number),
    year: types.maybeNull(types.number),
    eventType: types.optional(types.string, ''),
    isHoliday: types.optional(types.boolean, false),
    events: types.optional(types.array(eventModel), []),
})
// eslint-disable-next-line arrow-body-style
.actions((self) => ({

    getTodayEvents() {
        const { day } = self;
        const { month } = self;
        const { year } = self;
        self.changeLoading(true);
        self.changeHasError(false);

        request.get(`per/events/${day}/${month}/${year}?eventType='irn'`)
            .then((response) => {
                self.updateEvents(response.data.events.PersianCalendar);
                self.changeLoading(false);
                self.changeHasError(false);
            }).catch((error) => {
                console.log(error);
                self.changeHasError(true);
            });
    },
    updateEvents(data) {
        let events = [];

        data.map((item) => events = [...events, item]);
        self.events = [...events];
    },
    changeLoading(status) {
        self.loading = status;
    },
    changeHasError(status) {
        self.hasError = status;
    },
    updateDate(obj) {
        if (obj != null) {
            self.day = obj.day;
            self.month = obj.month;
            self.year = obj.year;
            self.getTodayEvents();
        }
    },
}));

export default (state) => eventStore.create(state || {});
