import React from 'react';
import {
 Box, CircularProgress, makeStyles, Button,
} from '@material-ui/core';
import { observer } from 'mobx-react';
import { useStore } from '../../hooks';
import EventCard from '../commons/Card';
import { months } from '../../utils';

const useStyles = makeStyles((theme) => {
    return {
        root: {
            flexGrow: 1,
            padding: theme.spacing(6),
            fontFamily: 'Vazir',
        },
        grid: {
            padding: theme.spacing(2),
            textAlign: 'center',
            color: theme.palette.text.secondary,
        },
    };
});

const Cards = () => {
    const classes = useStyles();
    const { eventStore } = useStore();
    const getMonth = (month) => {
        let currentMont = month;
        currentMont = months.filter((el) => { return el.number === currentMont; });
        return currentMont[0].title;
    };
    const events = eventStore.events.map((el, i) => {
        return (
            <Box key={i} m={2}>
                <EventCard day={`${el.day} ${getMonth(el.month)}`} isHoly={el.holiday} title={el.title} />
            </Box>
        );
    });
    if (eventStore.events.length !== 0) {
        return (
            <div className={classes.root}>
                <Box display="flex" flexWrap="wrap" justifyContent="center" alignItems="center" pt={4}>
                    {eventStore.loading ? <CircularProgress /> : events}
                </Box>
            </div>
        );
    } if (eventStore.hasError) {
        return (
            <div className={classes.root}>
                <Box display="flex" flexDirection="column" flexWrap="wrap" justifyContent="center" alignItems="center" pt={4}>
                    <p> مشکل در ارتباط با سرور</p>
                    <Button onClick={eventStore.getTodayEvents} variant="outlined">تلاش مجدد</Button>
                </Box>
            </div>
        );
    }

        return (
            <div className={classes.root}>
                <Box display="flex" flexWrap="wrap" justifyContent="center" alignItems="center" pt={4}>

                    {eventStore.loading ? <CircularProgress /> : <p>رویدادی موجود نیست</p>}
                </Box>
            </div>
        );
};

export default observer(Cards);
