import React from 'react';
import { Route } from 'react-router-dom';
import { Paper, Typography, Box } from '@material-ui/core';
import { Helmet } from 'react-helmet';
import NavberLink from '../../elemenets/NavbarLink';

function NotFound() {
    return (
        <Route
          render={({ staticContext }) => {
                if (staticContext) staticContext.statusCode = 404;
                return (
                    <Box display="flex" alignItems="center" justifyContent="center" p={5}>
                        <Helmet titleTemplate="%s - ایونتا">
                            <title>یافت نشد</title>
                        </Helmet>
                        <Paper elevation={3}>
                        <Box display="flex" flexDirection="column" alignItems="center" justifyContent="center" p={10}>

                            <Typography variant="h3" component="h2">
                                oops...!

                            </Typography>
                            <Typography variant="subtitle2">
                                صفحه ی مورد نظر یافت نشد

                            </Typography>
                                <NavberLink path="/" title="ناموسا برمیگردم" />

                        </Box>
                        </Paper>
                    </Box>


                );
            }}
        />
    );
}

export default NotFound;
