import React from 'react';
import { Helmet } from 'react-helmet';

import { Grid, Paper } from '@material-ui/core';
import Layout from '../../layouts';
import Cards from '../../elemenets/Cards';
import DatePicker from '../../elemenets/DatePicker';

const Home = () => {
    return (
        <Layout>
            <Helmet>
                <title>خانه</title>
            </Helmet>
            <Grid container>
                <Grid item xs={8}>
                    <Cards />
                </Grid>
                <Grid item xs={4}>
                    <DatePicker />
                </Grid>

            </Grid>


        </Layout>
    );
};

export default Home;
