
import React from 'react';
import { asyncComponent } from '@jaredpalmer/after';
import Notfound from './components/screens/NotFound';

const Loading = () => <div>...LOADING...</div>;
export default [
  {
    path: '/',
    exact: true,
    component: asyncComponent({
      loader: () => import('./components/screens/home'),
      Placeholder: <Loading/>, 
    }),
  },
  {
    path: '/about',
    exact: true,
    component: asyncComponent({
      loader: () => import('./components/screens/about'), // required
      Placeholder: <Loading />, // this is optional, just returns null by default
    }),
  },
  {
    path: '/convert',
    exact: true,
    component: asyncComponent({
      loader: () => import('./components/screens/convert'), // required
      Placeholder: <Loading />, // this is optional, just returns null by default
    }),
  },
  {
    component: Notfound,
  },
];
