/* eslint-disable no-param-reassign */
/* eslint-disable arrow-body-style */
import { types, getSnapshot } from 'mobx-state-tree';
import request from '../utils/api/request';

const searchModel = types.model('searchModel', {
    day: types.optional(types.number, 0),
    month: types.optional(types.number, 0),
})
.views((self) => ({
    dayValue() {
        return self.day;
    },
    monthValue() {
        return self.month;
    },
}))
.actions((self) => ({

    handleDay(day) {
        self.day = day;
    },
    handleMonth(month) {
        self.month = month;
    },

}));
export default (state) => searchModel.create(
    state || {},
);
